p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

ul {
    list-style: none;
    margin: 0;
    padding-left: 0;
}

img {
    display: block;
    width: 100%;
    max-width: 100%;
    height: auto;
}

textarea {
    resize: none;
}

body {
    font-family: var(--main-font);
    color: var(--main-text-color);
    background-color: var(--main-background-color);
     
}

.link {
    text-decoration: none;
    font-style: normal;
}

.no-scroll {
    overflow: hidden;
}